
* {
    font-family: Helvetica, Arial, sans-serif;
}

body {
    padding: 60px;
}

input {
    font-size: 1.5rem;
    width: 100%;
}
.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: space-between;
}
.card {
    animation: bounce;
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    margin-left: 10px;
    margin-top: 10px;
    overflow: hidden;
}
 .card p {
     padding: 5px;
     text-align: center;
 }
 .card img {
     max-height: 170px;
 }
